* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

*::-webkit-scrollbar {
    width: 0.5vw;
    height: 0.5vw;
    background-color: #f0f0f0;
}

*::-webkit-scrollbar-thumb {
    background: #cdcdcd;
}

:root {
    --border-radius-val: .5em;
    --main-color:#b5e0e1;
    --media-min: 800px;
}

#root {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    height: 100vh;
    background-color: var(--main-color);
    overflow-y: scroll;
}


@media (min-width: 800px) {
    *{
        font-size: 2vmin;
    }
    h1{
        font-size: 3vmin;
    }
    h2{
        font-size: 2.6vmin;
    }
    h3{
        font-size: 2.3vmin;
    }
}

@-moz-document url-prefix() {
    * {
        scrollbar-width: thin;
        /*mozilla scrollbar*/
    }

    li {
        margin-left: 1em;
    }
}